.bookclass-page {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  select, input[type="date"], .checkout-button {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .price-note {
    font-size: 12px;
    color: #888;
  }
  
  .checkout-button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .checkout-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .checkout-button:hover:not(:disabled) {
    background-color: #0056b3;
  }
  
  .error-message {
    color: red;
    font-size: 14px;
  }
  